<template>
  <div class="gdt">
    <img
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/headline/header.png"
      alt=""
      class="part1" />
    <div class="part6">
      <div class="tuikuan">- 开课3天内，对课程不满意无条件退还12元 -</div>
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请填写手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix"> +86 <span class="icon">▼</span> </span>
          </template>
        </VanField>
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请填写验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <div class="btn" @click="submit">
          <div class="submit">立即报名</div>
        </div>
      </VanForm>
    </div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>报名成功</p>
          <p>{{ el.time }}秒前</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/2248phpohoUV31608026265201215.jpg"
      alt=""
      class="part1" />
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/9435phpwui8261608026276201215.jpg"
      alt=""
      class="part1" />
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/4107php2sgE5q1608026287201215.jpg"
      alt=""
      class="part1" />
    <img
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/headline/why.png"
      alt=""
      class="part1" />
    <img
      src="https://oss-market.weimiaocaishang.com/Public/xun/InfoFlow/partA7.png"
      alt=""
      class="part1" />
    <div class="right">
      <div class="name">北京微淼财商科技有限公司</div>
      <div class="num">京ICP备19026951号</div>
    </div>
    <div class="footer">
      <div>
        <p style="text-align: center; font-size: 16px; font-weight: bold;">
          仅售
          <span
            style="font-size: 22px; color: #ff2c12; font-weight: bold;">12元</span>
        </p>
      </div>
      <a class="baoming" @click="submit">立即报名</a>
    </div>
    <BackDiaLog
      @confirm="pay"
      @cancel="stop"
      v-model="showDialog" />
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
  </div>
</template>
<script>
import { getSearch, calArray } from '@/utils'
import BackDiaLog from '@/components/common/StayDialog' // 支付挽留弹框
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import { codeMap } from './ErrorMsg'
export default {
  components: {
    BackDiaLog,
    PayTypeDialog,
    CountDown
  },
  data: function () {
    return {
      swipeList: [],
      form: {},
      isFinsh: true,
      showPaySelet: false,
      showDialog: false
    }
  },
  async created () {
    this.form = {
      phone: localStorage.getItem('Headline-phone'),
      code: localStorage.getItem('Headline-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.showDialog = true
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
    // 获取最新报名客户
    const res = await this.$apis.userOperator.getLatestUser()
    this.swipeList = calArray(res, 2)
  },

  beforeDestroy () {
    clearInterval(this.timer)
  },

  methods: {
    stop () {
      clearInterval(this.timer)
    },
    // 提交表单
    submit () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0245'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess()
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess () {
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      const medId = getSearch('medId')
      console.log(medId)
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.showDialog = false
          if (code === 30006) {
            if (medId === '3610') {
              window._shTrackSendLog({ trackId: '19', trackType: 'PURCHASE', convertId: '9160' })
            }
            return this.goSucccess()
          }
          return this.showMsg(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0244'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('Headline-phone', this.form.phone)
      localStorage.setItem('Headline-code', this.form.code)
      this.$apis.payment
        .payBeforeCheck(this.form)
        .then(async (res) => {
          this.showPaySelet = true
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
