    .tuikuan {
      font-family: PingFangSC-Medium;
      font-size: 26px;
      color: #38465d;
      margin-top: 38px;
      margin-bottom: 38px;
      text-align: center;
    }

    .wrap {
      position: relative;
    }

    .btn {
      .submit {
        width: 600px;
        height: 80px;
        background: -webkit-linear-gradient(top, #ff929d, #ff0034);
        background: -o-linear-gradient(bottom, #ff929d, #ff0034);
        background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
        background: linear-gradient(to bottom, #ff929d, #ff0034);
        border-radius: 3px;
        font-size: 40px;
        font-family: Source Han Sans CN;
        color: #fff;
