        color: #000;
        display: flex;
        align-items: center;

        .icon {
          font-size: 25px;
          margin-left: 10px;
          color: #3c81fd;
        }

        padding-right: 15px;
      }

      margin-bottom: 20px;
      width: 600px;
      height: 86px;
      background: #f5f8ff;
      border-radius: 3px;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }
}

.right {
  text-align: center;
  padding: 15px;

  div {
    margin-bottom: 20px;
  }
